@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

:root {
  --main-size: 1148px;
}

body {
  margin: 0;
  font-family: 'Space Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 400;
  color: #232323;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #232323;
}
li {
  list-style: none;
}
.underline {
  position: relative;
}
.underline::after {
  position: absolute;
  content: '';
  left: 0;
  bottom: 10%;
  width: 100%;
  border-bottom: #232323 1px solid;
}

header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 68px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 72px;
  border-bottom: 1.4px solid #8A8A8A;
  font-size: 21px;
  line-height: 21px;
  background-color: #fff;
  z-index: 100;
}

header .page_title {
  font-weight: 700;
}

header .page_nav {
  display: flex;
  gap: 27px;
}

main {
  width: var(--main-size);
  padding: 164px 0 185px 0;
  margin: 0 auto; 
}

main .introduction h1 {
  font-size: 44px;
  font-weight: 400;
  line-height: 61px;
  margin-bottom: 20px;
}
main .introduction p {
  font-size: 25px;
  line-height: 46px;
  margin-bottom: 56px;
}

main .projects {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px;
}
main .projects .project_summary {
  width: 100%;
  margin-bottom: 19px;
}
main .projects .project_summary .thumb_wrap {
  width: 100%;
  height: 319px;
  background-color: #c4c4c4;
}
main .projects .project_summary .thumb_wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
main .projects .project_summary .info {
  margin-top: 7px;
  font-size: 20px;
  line-height: 26px;
}
main .projects .project_summary .info .type {
  color: #838383;
}

footer {
  width: 100%;
  border-top: 1.4px solid #8A8A8A;
  font-size: 20px;
  line-height: 46px;
}
.footer_wrapper {
  width: var(--main-size);
  height: 300px;
  padding: 58px 0 82px 0;
  margin: 0 auto;
}
.footer_info {
  margin-bottom: 22px;
}
.copyright {
  color: #838383;
}